import React from "react";

import "./cardStyle.css";

const Card = props => {
  const [show, toggleShow] = React.useState(false);
  return (
    <div className="card text-center">
      <div className="overflow">
        <img src={props.imgsrc} alt="image1" className="card-img-top" />
      </div>
      <div className="card-body text-dark">
        <h4 className="card-title">{props.title}</h4>
        <button onClick={() => toggleShow(!show)} className="btn btn-dark">
          подробнее {show ? "" : ""}
        </button>
        {show && (
          <div>
            {" "}
            <div className="techimg">
              <img src={props.tech1} alt="" />
              <img src={props.tech2} alt="" />
              <img src={props.tech3} alt="" />
              <img src={props.tech4} alt="" />
              <img src={props.tech5} alt="" />
              <img src={props.tech6} alt="" />
              <img src={props.tech7} alt="" />
              <img src={props.tech8} alt="" />
            </div>
            <p className="card-text text-secondary">{props.description}</p>
            <a
              href={props.link1}
              target="_blank"
              rel="noopener noreferrer"
              className="btn btn-dark"
            >
              {props.buttontext1}
            </a>
            <a
              href={props.link2}
              target="_blank"
              rel="noopener noreferrer"
              className="btn btn-dark"
            >
              {props.buttontext2}
            </a>
          </div>
        )}
      </div>
    </div>
  );
};

export default Card;
