import React from "react";

import Typical from "react-typical";
import Particles from "react-particles-js";
import MessengerCustomerChat from "react-messenger-customer-chat";

import "./homepage.css";

const Home = () => {
  return (
    <div className="home-body">
      <Particles
        style={{
          position: "absolute",
          height: "100%",
          width: "100%",
          backgroundColor: "#202020",
          backgroundSize: "cover"
        }}
        params={{
          particles: {
            number: {
              value: 80,
              density: {
                enable: true,
                value_area: 800
              }
            },
            color: {
              value: "#ffffff"
            },
            shape: {
              type: "circle",
              stroke: {
                width: 0,
                color: "#000000"
              },
              polygon: {
                nb_sides: 5
              }
            },
            opacity: {
              value: 0.5,
              random: false,
              anim: {
                enable: false,
                speed: 2.3,
                opacity_min: 0.2,
                sync: true
              }
            },
            size: {
              value: 3,
              random: true,
              anim: {
                enable: false,
                speed: 40,
                size_min: 0.1,
                sync: false
              }
            },
            line_linked: {
              enable: true,
              distance: 50,
              color: "#ffffff",
              opacity: 0.4,
              width: 1
            },
            move: {
              enable: true,
              speed: 5,
              direction: "none",
              random: true,
              straight: false,
              out_mode: "out",
              bounce: false,
              attract: {
                enable: true,
                rotateX: 600,
                rotateY: 1200
              }
            }
          },
          interactivity: {
            detect_on: "canvas",
            events: {
              onhover: {
                enable: true,
                mode: "repulse"
              },

              resize: true
            },
            modes: {
              grab: {
                distance: 400,
                line_linked: {
                  opacity: 1
                }
              },

              repulse: {
                distance: 200,
                duration: 0.4
              },
              push: {
                particles_nb: 4
              },
              remove: {
                particles_nb: 2
              }
            }
          },
          retina_detect: true
        }}
      />
      <MessengerCustomerChat
        pageId="110130820573462"
        appId="138272724037473"
        theme_color="#999"
        logged_out_greeting="feel free to text me"
        logged_in_greeting="ask me anything"
      />
      <div className="contacts">
        <h1 className="home__title">Alexander Sevruk</h1>

        <h2 className="home__text">Front-end developer / Graphic designer</h2>

        <p className="home__typical">
          <Typical
            steps={["React", 2000, "Front-end", 2000, "Layout", 2000]}
            loop={Infinity}
            wrapper="b"
          />
        </p>
        <div className="social">
          {/*github*/}

          <a
            href="https://github.com/AlexanderSevruk"
            rel="noopener noreferrer"
            target="_blank"
          >
            <i class="fa fa-github"></i>
          </a>

          {/*telegram*/}

          <a
            href="https://t.me/AlexanderSevruk"
            rel="noopener noreferrer"
            target="_blank"
          >
            <i class="fa fa-telegram"></i>
          </a>

          {/*mail*/}

          <a
            href="mailto: alexandersevruk96@gmail.com"
            rel="noopener noreferrer"
            target="_blank"
          >
            <i class="fa fa-comment"></i>
          </a>
        </div>
      </div>
    </div>
  );
};

export default Home;
